import { motion } from "framer-motion";
import HtmlIcon from "@mui/icons-material/Html";
import CssIcon from "@mui/icons-material/Css";
import JavascriptIcon from "@mui/icons-material/Javascript";
import LanguageIcon from "@mui/icons-material/Language";

const Skills = () => {
  const skills = [
    { name: "HTML", icon: <HtmlIcon /> },
    { name: "CSS", icon: <HtmlIcon /> },
    { name: "JavaScript", icon: <HtmlIcon /> },
    { name: "React", icon: <HtmlIcon /> },
    { name: "Node", icon: <HtmlIcon /> },
    { name: "Express", icon: <HtmlIcon /> },
    { name: "MongoDB", icon: <HtmlIcon /> },
    { name: "Android", icon: <HtmlIcon /> },
    { name: "iOS", icon: <HtmlIcon /> },
  ];

  return (
    <section id="skills">
      <h2 className="head-text app__heading">
        My <span>Skills</span>
      </h2>
      <div className="app__skills">
        <div className="app__skills-container">
          <motion.div className="app__skills-list">
            {skills.map((skill) => (
              <motion.div
                whileInView={{ opacity: [0, 1] }}
                transition={{ duration: 0.5 }}
                className="app__skills-item app__flex"
                key={skill.name}
              >
                <div
                  className="app__flex"
                  style={{ backgroundColor: skill.bgColor }}
                >
                  {skill.icon}
                </div>
                <p className="p-text">{skill.name}</p>
              </motion.div>
            ))}
          </motion.div>
        </div>
      </div>
    </section>
  );
};

export default Skills;
